/* App.css */

/* Overall layout styles */
.app {
  display: flex;
  flex-direction: row-reverse; /* Stack items vertically on small screens */
  align-items: center;
  justify-content: center;
  margin-top: 50px;
  font-family: Arial, sans-serif;
}

.phone-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* Occupy full width */
  max-width: 800px; /* Limit max width for larger screens */
  margin-bottom: 20px; /* Space between phone screen and app details */
}

.phone-screen {
  position: relative;
}

.phone-frame {
  width: 320px; /* Adjust width for phone frame */
  height: auto;
  border: 1px solid #ccc;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.phone-screenshot {
  width: 100%; /* Fill container */
  height: auto;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.app-details {
  max-width: 400px; /* Adjust max width for app details */
  text-align: center;
}

.app-details h1 {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.app-details p {
  font-size: 16px;
  line-height: 1.6;
  color: #666;
}

.buttons {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap; /* Allow buttons to wrap on small screens */
  justify-content: center; /* Center align buttons */
}

button {
  margin: 5px; /* Adjust spacing between buttons */
  padding: 12px 24px;
  font-size: 16px;
  color: #fff;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

.button-icon {
  margin-right: 8px;
  vertical-align: middle;
}

@media (max-width: 768px) {
  /* Adjustments for smaller screens (like phones) */
  .app {
    padding: 20px;
    flex-direction: column; /* Add padding to app container */
  }

  .phone-section {
    flex-direction: column; /* Stack phone screen above app details on small screens */
  }

  .phone-frame {
    width: 100%; /* Fill container width */
    max-width: 100%; /* Fill container width */
    margin-bottom: 20px; /* Space between phone screen and app details */
  }

  .phone-screenshot {
    width: 100%; /* Fill container width */
    max-width: 100%; /* Fill container width */
    margin-bottom: 20px; /* Space between phone screen and app details */
  }

  .app-details {
    max-width: 100%; /* Adjust max width for app details */
  }
}
